.app-wrapper {
  display: grid;
  grid-template-areas: "header header"
  "navigation content";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr  10fr;
  grid-gap: 5px;
  max-width: 1440px;
  margin: 0 auto;
  overflow: hidden;
  padding: 20px;
}

.app-wrapper-content {
  grid-area: content;
  background-color: rgb(1, 79, 82);
  border: 1px solid white;
  border-radius: 10px;
}